.dataTable {
  background-color: white;
  border-collapse: separate;
  border-spacing: 0;
  border-bottom: 1px solid #111;
}

.dataTable thead th {
  padding: 10px 18px;
  border-bottom: 1px solid #111;
  font-weight: bold;
  cursor: pointer;
}

.dataTable thead .sorting,
.dataTable thead .sorting_asc,
.dataTable thead .sorting_desc {
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: center right;
}

.dataTable thead .sorting {
  background-image: url("./assets/images/sort_both.png");
}

.dataTable thead .sorting_asc {
  background-image: url("./assets/images/sort_asc.png");
}

.dataTable thead .sorting_desc {
  background-image: url("./assets/images/sort_desc.png");
}

.dataTable tbody tr:nth-child(odd) {
  background-color: #f9f9f9;
}

.dataTable tbody tr:first-child td {
  border-top: none;
}

.dataTable tbody td {
  padding: 8px 10px;
  border-top: 1px solid #ddd;
}

.dataTable tr:nth-child(even) .current-column {
  background-color: #fafafa;
}

.dataTable tr:nth-child(odd) .current-column {
  background-color: #f1f1f1;
}

.paginate_current {
  font-weight: bolder;
}

/** UTILITIES **/
/* display */
.d-grid {
  display: grid !important;
}

.d-flex {
  display: flex !important;
}

/* spacement */
.mt-3 {
  margin-top: 1rem !important;
}

.mb-2 {
  margin-bottom: 0.5rem !important;
}

.gap-1 {
  gap: 0.25rem !important;
}

/* alignement */
.justify-content-between {
  justify-content: space-between !important;
}

.align-items-center {
  align-items: center !important;
}
